const styles = {
  infoContainer: {
    boxShadow: "0 2px 4px #bbb",
    padding: "0 15px 15px 15px",
    borderRadius: "5px",
    marginTop: "15px",
    background: "#fff",
  },

  iconContainer: {
    width: "40px",
    marginRight: "20px",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },

  mainInfo: {
    display: "flex",
    alignItems: "center",
    paddingTop: "10px"
  },

  info: {
    fontWeight: 300,
    paddingTop: "5px"
  },

  icon: {
    fontSize: "24px",
    color: "#ddd"
  },
}