import React, { useState } from 'react'

export default function Info() {
  const [education, setEducation] = useState("i hc Phng ng")
  const [experience, setExperience] = useState("CASHIER - 09/2017 n 05/2019 SEARS - CA HNG,  H Ni Cung cp dch v khch hng c bit  phn bit v qung b thng hiu cng ty. Hp tc vi cc thnh vin nhm dch v khch hng  cung cp dch v c bit trong ton b tri nghim mua sm v mua hng. Gi dng thanh ton sch s mi lc v duy tr mn hnh sn phm gn gng, ngn np. Mentor v hun luyn vin thu ngn mi.")
  const [certificate, setCertificate] = useState("Chng ch Ting Anh Toeic - 2018")
  const [other, setOther] = useState("")
  // const [row, setRow] = useState(10)

  const handleEducation = (event) => {
    setEducation(event.target.value);
  }
  const handleExperience = (event) => {
    setExperience(event.target.value);
  }
  const handleCertificate = (event) => {
    setCertificate(event.target.value);
  }
  const handleOther = (event) => {
    setOther(event.target.value);
  }

  const handleSubmit = (event) => {
    alert('An essay was submitted: ');
    event.preventDefault();
  }

  return (
    <React.Fragment>
      <form onSubmit={handleSubmit} style={styles.form}>
        <h4 style={styles.text}>Hc vn</h4>
        <input style={styles.input} type="text" onChange={handleEducation} value={education} />

        <h4 style={styles.text}>Kinh nghim lm vic</h4>
        <textarea
          style={styles.textarea}
          rows={10}
          value={experience}
          onChange={(event) => {
            handleExperience(event)
          }} />

        <h4 style={styles.text}>Chng ch</h4>
        <input style={styles.input} type="text" onChange={handleCertificate} value={certificate} />

        <h4 style={styles.text}>K nng khc</h4>
        <input style={styles.input} type="text" onChange={handleOther} value={other} />

        <button style={styles.submitBtn} type="submit" >
          Lu
        </button>
      </form>
    </React.Fragment>
  )
}


const styles = {
  text: {
    paddingBottom: "5px"
  },

  form: {
    marginTop: "20px",
    marginBottom: "10px",
    width: "100%",
    height: "auto",
    padding: "0 15px",
  },

  input: {
    width: "100%",
    height: "40px",
    background: "#fff",
    border: "1px solid #eee",
    borderRadius: "5px 0 0 5px",
    padding: "0 10px",
    marginBottom: "15px"
  },

  textarea: {
    width: "100%",
    resize: "none",
    minHeight: "40px",
    border: "1px solid #eee",
    borderRadius: "5px 0 0 5px",
    padding: "10px",
    marginBottom: "15px"
  },

  submitBtn: {
    width: "100%",
    height: "40px",
    color: "#fff",
    display: "flex",
    border: "none",
    borderRadius: "5px",
    backgroundImage: "linear-gradient(to top left, #5160FF, #45B6FF)",
    justifyContent: "center",
    alignItems: "center"
  },
}