import React from 'react'
import Noti from '../../../components/noti'
import FeaturedNoti from '../../../components/featuredNoti'
import Title from '../../../components/title'

export default function Notification() {
  return (
    <div style={styles.container}>
      <Title title="MI NHT" />
      <FeaturedNoti iconName="fa-search" time="1 ting trc" url="/findjob" />
      <FeaturedNoti iconName="fa-briefcase" time="5 ting trc" url="/recruiting" />

      <Title title="TUN NY" />
      <Noti iconName="fa-briefcase" time="2 ngy trc" url="/recruiting" />
      <Noti iconName="fa-search" time="4 ngy trc" url="/findjob" />
      <Noti iconName="fa-briefcase" time="6 ngy trc" url="/recruiting" />
      <Noti iconName="fa-search" time="7 ngy trc" url="/findjob" />
      <Noti iconName="fa-briefcase" time="7 ngy trc" url="/recruiting" />
    </div>
  )
}

const styles = {
  container: {
    marginTop: "60px",
  },

  title: {
    fontWeight: 400,
    padding: "15px 0",
  },
}